:root {
  --PrimaryColor: hsl(26, 93%, 50%);
  --HoverColor: hsl(26, 90%, 42%);
  --whiteColor: hsl(0, 0%, 100%);
  --blackColor: hsl(0, 0%, 18%);
  --textColor: hsl(240, 1%, 48%);
  --greyBg: hsl(0, 0%, 96%);
  --greyText: rgb(190, 190, 190);
  --inputColor: hsl(330, 12%, 97%);
  //box-shadow: 0 2px 8px 2px rgba(178, 178, 178, 0.334);
}

.popular {
  padding-top: 15rem;

  .secContainer {
    .secHeader {
      flex-direction: column;
      align-items: center;
      gap: 1rem;

      .textDiv {
        .secTitle {
          color: var(--blackColor);
          line-height: 1.5rem;
          padding-bottom: 0.5rem;
        }
      }

      .iconsDiv {
        gap: 0.5rem;
        .icon {
          padding: 0.5rem;
          background: var(--greyText);
          border-radius: 50%;
          font-size: 2.5rem;
          &:hover {
            background: var(--blackColor);
            color: var(--whiteColor);
          }
        }

        .leftIcon {
          background: var(--blackColor);
          color: var(--whiteColor);
          &:hover {
            background: var(--greyText);
            color: var(--blackColor);
          }
        }
      }
    }

    .mainContent {
      padding: 2rem 0;
      gap: 1rem;

      .singleDestination {
        overflow: hidden;
        border-radius: 1rem;
        box-shadow: 0 2px 8px 2px rgba(178, 178, 178, 0.334);

        .destImage {
          height: 200px;
          width: 100%;
          position: relative;
          overflow: hidden;
          img {
            height: 100%;
            width: 100%;
            object-fit: cover;
          }

          .overlayInfo {
            h3 {
              color: var(--whiteColor);
              font-weight: 600;
            }

            p {
              color: var(--whiteColor);
              opacity: 0.7;
              font-size: 13px;
            }

            .icon {
              position: absolute;
              right: 10px;
              bottom: 10px;
              padding: 0.5rem;
              background: var(--PrimaryColor);
              font-size: 2.5rem;
              border-radius: 50%;
              color: var(--whiteColor);
              border: 1px solid transparent;
              transition: 0.3s ease;

              &:hover {
                background: var(--blackColor);
                border: 1px solid var(--PrimaryColor);
                color: var(--PrimaryColor);
              }
            }
          }
        }

        .destFooter {
          padding: 1rem;

          .number {
            font-size: 2rem;
            font-weight: 700;
            color: var(--blackColor);
            position: relative;
            width: max-content;
            &::after {
              content: "";
              position: absolute;
              background: var(--PrimaryColor);
              top: 45%;
              right: -150%;
              height: 5px;
              width: 45px;
              border-radius: 1rem;
            }
          }

          .destText {
            justify-content: space-between;

            h5 {
              color: var(--blackColor);
              font-size: 20px;
            }
            .flex {
              font-size: 16px;
              font-weight: 600;
              align-items: flex-start;
              transform: translateY(5px);

              .icon {
                color: var(--PrimaryColor);
              }
            }
          }
        }
      }
    }
  }
}

@media screen and (min-width: 480px) {
  .popular {
    padding-top: 10rem;
  }
}

@media screen and (min-width: 556px) {
  .popular {
    .secContainer {
      .secHeader {
        width: 100%;
        flex-direction: row;
        justify-content: space-between;
        align-items: center;

        .textDiv {
          text-align: start;
          max-width: 70%;
        }

        .iconsDiv {
          justify-content: flex-end;
        }
      }
    }
  }
}

@media screen and (min-width: 720px) {
  .popular {
    padding-top: 7rem;
  }
}

@media screen and (min-width: 1024px) {
  .popular {
    padding-top: 10rem;

    .secContainer {
      .secHeader {
        .textDiv {
          .secTitle {
            font-size: 2.2rem;
          }
        }
      }
    }
  }
}
